<template>
  <div>
    <Input
      v-model="data.search"
      placeholder="Search Everything"
      class="w-full mb-5 mr-4"
      icon
    >
      <template #icon>
        <span class="material-icons">search</span>
      </template>
    </Input>
    <Tab
      :items="[
        {
          name: 'Requested',
          to: {
            name: 'settingManageLocationLinkedLocations',
            params: { linkedStatus: 2 }
          }
        },
        {
          name: 'Approved',
          to: {
            name: 'settingManageLocationLinkedLocations',
            params: { linkedStatus: 1 }
          }
        }
      ]"
    ></Tab>
    <div v-loading="loading.all">
      <Table :data="state.data">
        <TableColumn label="Store Name" min-width="250">
          <template #default="{ row }">
            <p class="font-semibold text-teal-04">{{ row.location_name }}</p>
          </template>
        </TableColumn>
        <TableColumn label="Email">
          <template #default="{ row }">
            {{ row.email || '-' }}
          </template>
        </TableColumn>
        <TableColumn label="Location" prop="address_text" />
        <TableColumn label="Phone">
          <template #default="{ row }">
            <span>{{ phoneNumber(row.phone_number) }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="Account Source"
          prop="company_name"
          min-width="200"
        />
        <TableColumn
          label="Date Linked"
          prop="date_linked"
          min-width="200"
          width="150"
        />
        <TableColumn label="" width="400">
          <template #default="{ row }">
            <div class="flex items-center flex-wrap">
              <Button
                v-if="route.params.linkedStatus == 1"
                @click="handleWidgetCode(row)"
                class="mr-4"
                color="teal"
                view="smooth"
                dense
              >
                <i class="material-icons">visibility</i> Widget
              </Button>
              <div
                class="lg:opacity-0 group-hover:opacity-100 transition-opacity"
              >
                <template v-if="route.params.linkedStatus == 2">
                  <Button
                    @click="handleApproveFranchiseeRequest(row.id)"
                    :loading="loadingFranchisee.approveFranchisee.get(row.id)"
                    class="mr-4"
                    color="green"
                    view="smooth"
                    dense
                    ><i class="material-icons">check</i> Approve</Button
                  >
                  <Button
                    :loading="loadingFranchisee.rejectFranchisee.get(row.id)"
                    @click="handleRejectFranchiseeRequest(row.id)"
                    color="red"
                    view="smooth"
                    dense
                    ><i class="material-icons">close</i> Reject</Button
                  >
                </template>
                <template v-if="route.params.linkedStatus == 1">
                  <Button
                    :loading="loadingFranchisee.revokeFranchisee.get(row.id)"
                    @click="handleRevokeFranchisee(row.id)"
                    color="red"
                    view="smooth"
                    dense
                    ><i class="material-icons">close</i> Revoke Access</Button
                  >
                </template>
              </div>
            </div>
          </template>
        </TableColumn>
      </Table>
      <Pagination
        v-model:page="data.page"
        v-model:limit="data.limit"
        :data="state"
      />
    </div>
    <WidgetCode
      v-model:show="showFranchiseeWidgetModal"
      :id="selectedFranchiseeWidget.id"
      :code="selectedFranchiseeWidget.code"
      :type="selectedFranchiseeWidget.type"
      @update-data="handleUpdateLinkedLocation"
    ></WidgetCode>
  </div>
</template>

<script>
import { defineComponent, watch, onMounted, reactive, ref } from 'vue'
import { useStore } from 'vuex'
import { useRoute, useRouter } from 'vue-router'

import {
  useStoreLocation,
  useFranchiseeStoreLocation,
  useFranchisee
} from 'composable/location'
import { useUser } from 'composable/user'

import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import Pagination from 'components/Pagination.vue'

import debounce from 'utils/debounce'
import { phoneNumber } from 'utils/number'
import { ElMessage } from 'element-plus'
import WidgetCode from 'components/ManageLocation/WidgetCode.vue'

export default defineComponent({
  components: {
    Card,
    Button,
    Checkbox,
    Modal,
    Input,
    Select,
    Pagination,
    Badge,
    Tab,
    WidgetCode
  },
  async setup(props, { emit }) {
    const store = useStore()
    const { data, state, loading, actionAll } = useFranchiseeStoreLocation()
    const { actionAll: actionAllStoreLocation } = useStoreLocation()
    const {
      loading: loadingFranchisee,
      actionRequestFranchisee,
      actionApproveFranchisee,
      actionRejectFranchisee,
      actionRevokeFranchisee
    } = useFranchisee()
    const { me, action: actionMe } = useUser()
    const route = useRoute()
    const router = useRouter()
    const showFranchiseeWidgetModal = ref(false)
    const selectedFranchiseeWidget = reactive({
      id: null,
      code: null,
      type: 'on_page'
    })

    watch(
      () => ({
        limit: data.limit,
        search: data.search
      }),
      () => {
        data.page = 1
      },
      {
        deep: true
      }
    )
    watch(
      () => ({
        ...data,
        linkedStatus: route.params.linkedStatus
      }),
      debounce(async ({ linkedStatus }) => {
        if (linkedStatus) await actionAll(route.params.linkedStatus)
      }, 500),
      {
        deep: true
      }
    )

    const handleRequestFranchisee = async (item) => {
      const data = {
        location_id: item.id,
        franchisor_code: item.franchisor_code ? item.franchisor_code : ''
      }
      await actionRequestFranchisee(data, async () => {
        await Promise.all([actionAll(), actionLocations()])
      })
    }
    const handleApproveFranchiseeRequest = async (locationId) =>
      await actionApproveFranchisee(locationId, async () => {
        ElMessage({
          type: 'success',
          message: 'Request approved'
        })
        await actionAll(route.params.linkedStatus)
      })
    const handleRejectFranchiseeRequest = async (locationId) =>
      await actionRejectFranchisee(locationId, async () => {
        ElMessage({
          type: 'success',
          message: 'Request rejected'
        })
        await actionAll(route.params.linkedStatus)
      })
    const handleRevokeFranchisee = async (locationId) =>
      await actionRevokeFranchisee(locationId, async () => {
        ElMessage({
          type: 'success',
          message: 'Linked location revoked'
        })
        await actionAll(route.params.linkedStatus)
      })
    const handleWidgetCode = (data) => {
      showFranchiseeWidgetModal.value = true
      Object.assign(selectedFranchiseeWidget, {
        id: data.id,
        code: data.widget_source,
        type: data.widget_app_type
      })
    }
    const handleUpdateLinkedLocation = async () => {
      await actionAll(route.params.linkedStatus)
    }

    onMounted(async () => {
      if (!route.params.linkedStatus)
        return router.push({
          name: route.name,
          params: {
            linkedStatus: 2
          }
        })

      if (!store.state.setting.manageLocation.all)
        await actionAllStoreLocation(store.state.user.me.id)
      await Promise.all([actionAll(route.params.linkedStatus)])
    })

    return {
      data,
      me,
      store,
      state,
      loading,
      loadingFranchisee,
      route,
      phoneNumber,
      selectedFranchiseeWidget,
      showFranchiseeWidgetModal,
      handleRequestFranchisee,
      handleApproveFranchiseeRequest,
      handleRejectFranchiseeRequest,
      handleRevokeFranchisee,
      handleWidgetCode,
      handleUpdateLinkedLocation
    }
  }
})
</script>
