<template>
  <Modal v-model="show" title="Widget">
    <template #default>
      <div class="mb-6">
        <Fieldset>
          <template #label>Widget Code</template>
          <template #default>
            <p class="my-2">
              Copy the following code and paste it as close to the opening
              &lthead&gt tag as possible on every page you wish to display the
              buyback widget.
            </p>
            <div
              class="border border-grey-02 bg-grey-lighten rounded rounded-lg p-4 my-6"
            >
              <code>{{ code }}</code>
            </div>
          </template>
        </Fieldset>
        <div class="flex flex-wrap justify-end mt-4">
          <Button @click="copyText(code)" color="teal" view="smooth" dense>
            <i class="material-symbols-outlined mr-2">content_copy</i> Copy
          </Button>
        </div>
      </div>
      <Fieldset>
        <template #label> Widget Type</template>
        <template #default>
          <label class="mb-4">Widget Type</label>
          <div class="flex flex-col">
            <div class="flex flex-wrap items-center">
              <Radio
                v-model="data.widget_app_type"
                class="font-normal mb-2"
                value="floating"
              >
                <template #default> Floating </template>
              </Radio>
              <Tooltip
                placement="right"
                content="This widget will open when clicking on a floating button on the side of the page."
              >
                <i class="text-grey-03 material-icons ml-2 cursor-pointer"
                  >info</i
                >
              </Tooltip>
            </div>
            <div class="flex flex-wrap items-center">
              <Radio
                v-model="data.widget_app_type"
                class="font-normal"
                value="on_page"
              >
                <template #default>
                  <div class="flex flex-wrap items-center">On Page</div>
                </template>
              </Radio>
              <Tooltip
                placement="right"
                content="This widget will be hosted directly on a webpage."
              >
                <i class="text-grey-03 material-icons ml-2 cursor-pointer"
                  >info</i
                >
              </Tooltip>
            </div>
          </div>
        </template>
      </Fieldset>
    </template>
    <template #footer>
      <Button
        @click="handleUpdate"
        :loading="loading.update"
        color="teal"
        view="smooth"
        >Save</Button
      >
    </template>
  </Modal>
</template>

<script>
import { ref, computed, defineComponent, watch } from 'vue'
import { useFranchiseeLocationWidget } from 'composable/location'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Radio from 'bbUi/components/radio/radio.vue'

import copyText from 'utils/copy'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Modal,
    Button,
    Fieldset,
    Checkbox,
    Radio
  },
  props: {
    id: {
      type: [Number, String],
      default: ''
    },
    code: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'on_page'
    },
    show: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:show', 'update-data'],
  setup(props, { emit }) {
    const { loading, data, resetData, actionUpdate } =
      useFranchiseeLocationWidget()
    const show = computed({
      set: (newVal) => {
        if (newVal === false) resetData()
        emit('update:show', newVal)
      },
      get: () => props.show
    })
    const code = ref('')
    watch(
      () => ({ id: props.id, type: props.type, show: props.show }),
      ({ id, type }) => {
        ;(data.id = id), (data.widget_app_type = type)
      },
      {
        deep: true
      }
    )
    watch(
      () => props.code,
      () => {
        code.value = props.code
      }
    )
    const handleUpdate = async () => {
      await actionUpdate((response) => {
        const { widget_app_type, widget_source } = response
        code.value = widget_source
        data.widget_app_type = widget_app_type
        ElMessage({
          type: 'success',
          message: 'Changes saved'
        })
        emit('update-data')
      })
    }
    return {
      loading,
      data,
      show,
      code,
      copyText,
      handleUpdate
    }
  }
})
</script>
